@import 'utils.css';

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

:root {
    --white: #fff;
    --whiteborder: #ddd;
    --lightwhite: #effbfe;
    --grayborder: #ededed;
    --lightgray: #aaa;
    --gray: #777;
    --black: #150605;
    --lightblue: #ace6ec;
    --lightred: #f47b6e;
    --red: #c26434;
}

body {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    margin: 0;
    overflow-x: hidden;
    background-color: var(--white);
    font-family: "Nunito", Helvetica, sans-serif;
}

.body-container {
    flex-grow: 1;
}